import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Background from '../../assets/background.png';
import Form from '../lib/Form';
import TextField from '../lib/TextField';
import PrimaryButton from '../lib/Button/PrimaryButton';
import CardPanel from '../lib/Card';
import Container from './Container';
import { sendPasswordReset } from '../../actions/api/auth';
import { notifyError, notifySuccess } from '../../actions/notification';
import language from '../../constants/language';

const styles = {
  root: {
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundImage: `url(${Background})`,
    flex: 1,
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  formTitle: {
    paddingLeft: '2.5%',
    paddingTop: '3%',
  },
  formInput: {
    paddingLeft: '5%',
    paddingRight: '5%',
    paddingBottom: '5%',
  },
  welcome: {
    marginBottom: 20,
    color: 'white',
    fontSize: 20,
  },
  title: {
    marginBottom: 60,
    color: 'white',
    fontSize: 50,
    fontWeight: 'bold',
    alignItems: 'felx-end',
  },
  logo: {
    color: '#ea5941',
    fontSize: 80,
    marginBottom: -13,
    marginRight: 10,
  },
};

class ForgotContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      redirect: false,
    };

    this.handleEmail = this.handleEmail.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentWillUnmount() {
    this.isUnmounted = true;
  }

  handleEmail(event) {
    const value = event;
    this.setState({
      email: value,
    });
  }

  handleSubmit(event) {
    event.preventDefault();
    const { email } = this.state;
    const { selectedLanguage } = this.props;
    this.props.sendPasswordReset({ email })
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess(language[selectedLanguage].login.resetPasswordEmailSent);
          this.setState({ redirect: true });
        }
      });
  }

  render() {
    const { email, redirect } = this.state;
    const { classes, selectedLanguage } = this.props;
    if (this.props.isAuthenticated) {
      return <Redirect to="/welcome" />;
    }
    if (redirect) {
      return <Redirect to="/login" />;
    }
    return (
      <Container>
        <CardPanel>
          <Typography className={classes.formTitle} variant="headline" component="h2">
            {language[selectedLanguage].login.forgotPass}
          </Typography>
          <Form
            onSubmit={this.handleSubmit}
            className={classes.formInput}
          >
            <TextField
              onChange={this.handleEmail}
              type="email"
              name={email}
              fullWidth
              label={language[selectedLanguage].login.email}
              required
              id="user"
              autoFocus
            />
            <PrimaryButton
              type="submit"
            >
              {language[selectedLanguage].login.resetPassword}
            </PrimaryButton>
          </Form>
        </CardPanel>
      </Container>
    );
  }
}

ForgotContainer.propTypes = {
  classes: PropTypes.any.isRequired,
  isAuthenticated: PropTypes.bool.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  sendPasswordReset: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

function mapStateToProps(state) {
  return {
    errorMsg: state.auth.errorMessage,
    isAuthenticated: state.auth.isAuthenticated,
    successMessage: state.auth.successMessage,
    selectedLanguage: state.user.language,
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ notifyError, notifySuccess, sendPasswordReset }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ForgotContainer));
